<template>
  <div class="order_wrapper">
    <Header></Header>
    <div class="order_content">
      <el-tabs v-model="active_order" >
        <el-tab-pane label="当前委托" name="current">当前委托</el-tab-pane>
        <el-tab-pane label="历史委托" name="history">历史委托</el-tab-pane>
      </el-tabs>

      <el-row>
        <el-col class="filter_head">

        </el-col>
        <el-col class="">
          <el-table></el-table>
        </el-col>
      </el-row>
    </div>  
  </div>
</template>

<script>
  import Mock from '@/utils/mock'
  import Header from '@/components/header/header'

  export default {
    components: {Header},
    props: {},
    data() {
      return {
        active_order:'current'
      }
    },
    watch: {},
    computed: {},
    methods: {},
    created() {},
    mounted() {}
  };
</script>
<style lang="scss" scoped>
  @import '@/style/base.scss';

  .order_wrapper{
    width: 100%;
    min-height: 100%;
    background-color: $other_bg;
    min-width: 1280px;
    padding-top: 60px;
    padding-bottom: 15px;
    box-sizing: border-box;
    .order_content{
      width: 1200px;
      background-color: $light_font_color;
      margin:0 auto;
      padding: 0 20px;
    }
  }
</style>


